* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  color: #fff;
  -webkit-backdrop-filter: blur(15rem);
  backdrop-filter: blur(15rem);
  background-repeat: no-repeat;
  background-size: cover;
}

.header {
  display: flex;
}

h1 {
  margin-top: auto;
  margin-bottom: auto;
  position: relative;
}

svg {
  display: none;
}

.navButtons {
  margin-right: 2rem;
}

a {
  color: #fff;
  pad: 3rem;
  font-size: medium;
  text-decoration: none;
  display: flex;
}

p {
  text-align: left;
  margin-top: .1rem;
  margin-left: 1rem;
}

h3 {
  text-align: justify;
}

.aboutMe {
  margin-top: 8rem;
}

h2 {
  text-align: left;
  margin: 0 0 .3rem 1rem;
}

.aboutMeBody {
  text-align: justify;
  margin: .3rem 2rem 0;
}

.aboutMeHeader {
  margin-bottom: .5rem;
  font-size: 2rem;
}

.border {
  border-bottom: .05rem solid #ffffff4d;
  margin: 1em 0;
  padding: 0 6em;
}

label {
  text-align: center;
  grid-template-columns: 42.5% 15% 42.5%;
  margin-bottom: 3rem;
  font-size: 4rem;
  display: grid;
}

.special {
  border-bottom: .1rem solid #ffffff69;
  justify-content: center;
  margin: auto 2rem;
}

.link-logos {
  margin: auto 0;
}

nav {
  grid-column: 1 / 2;
}

.header-logo {
  min-height: 10vh;
  grid-template-columns: auto auto;
  align-items: center;
  display: grid;
}

.navigation {
  text-align: end;
}

section {
  height: 90vh;
  justify-content: center;
  align-items: center;
  display: flex;
}

.hero {
  height: 60%;
  width: 100%;
  position: relative;
}

.hero img {
  height: 100%;
  width: 100%;
  object-fit: cover;
  object-position: 100% 0;
}

.headline {
  color: #fff;
  font-size: 4rem;
  position: absolute;
  top: 70%;
  left: 15%;
  transform: translate(-20%, -70%);
}

button {
  background-color: #fff0;
  border: none;
}

.navbar-button {
  margin-left: 1rem;
  text-decoration: none;
}

.grid-container {
  grid-template-columns: 43rem 43rem;
  justify-content: center;
  display: grid;
}

.grid-item {
  text-align: center;
  background-color: #00000056;
  border-radius: .8rem;
  margin-bottom: 3rem;
  margin-left: 5rem;
  margin-right: 5rem;
  padding: 2rem;
}

form {
  grid-template-columns: auto;
  justify-content: center;
  display: grid;
}

.contactMeButton {
  border: none;
  border-radius: .3rem;
  padding: .25rem;
  box-shadow: .1rem .1rem #0000005a;
}

#itch {
  grid-template-columns: auto;
  justify-content: center;
  display: grid;
}

#smallTextarea {
  display: none;
}

.text-container {
  grid-template-columns: 45% 55%;
  margin-left: 2rem;
  font-size: large;
  display: grid;
}

.post {
  background-color: #00000046;
  border-radius: .8rem;
  padding: .5rem;
}

.post-image {
  margin: auto;
}

.post-image > img {
  height: auto;
  width: 700px;
  border-radius: .8rem;
}

li {
  line-height: 2rem;
}

.aboutMeContainer {
  grid-template-columns: 1fr 2fr 1fr;
  display: grid;
}

.aboutMe {
  grid-column: 2;
  font-size: 1.3rem;
  line-height: 2rem;
}

@media screen and (max-width: 1250px) {
  .grid-container {
    grid-template-columns: auto;
    justify-content: center;
    display: grid;
  }
}

@media screen and (max-width: 768px) {
  .grid-container {
    grid-template-columns: auto;
    justify-content: center;
  }

  .navigation {
    flex-direction: column;
    display: flex;
    position: relative;
  }

  svg {
    display: block;
  }

  .svg-icon {
    margin: 0 1rem 0 2.5rem;
    position: absolute;
  }

  #nav {
    margin-right: 2rem;
    display: none;
  }

  .navButtons {
    z-index: 1;
    flex-direction: column;
    margin-top: 2.5rem;
    display: flex;
    position: absolute;
  }

  a {
    justify-content: right;
    margin: .7rem 0 0 -1rem;
  }

  #itch {
    z-index: -1;
    margin-top: 10rem;
  }

  iframe {
    width: auto;
    height: 83px;
  }

  form {
    margin-top: 5rem;
  }

  label {
    text-align: center;
    grid-template-columns: 40% 20% 40%;
    font-size: 2rem;
  }

  .special {
    border-bottom: .1rem solid #ffffff69;
    justify-content: center;
    margin: auto 2rem;
  }

  #largeTextarea {
    display: none;
  }

  #smallTextarea {
    display: block;
  }
}

